//header block
.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: max-content;
  background: rgba($greyDark, 0.8);
  z-index: 2;
}

.header__inner{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 320px;
  padding: 15px;
  margin: 0 auto;
}

.logo{
  z-index: 2;
}

.logo__text{
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $white;
}

.buttons__nav-call{
  width: 23px;
  height: 18px;
  display: flex;
  justify-content: center;
  align-items: start;
  z-index: 2;

  .button{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
    border: none;
    padding: 0;
  }

  .button__line,
  .button__line:before,
  .button__line:after{
    content: "";
    position: absolute;
    height: 3px;
    width: 100%;
    background-color: $white;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    transition: 0.3s ease-in-out;
  }

  .button__line:before{
    top: -15px;
  }

  .button__line:after{
    bottom: -15px;
  }

}

.navigation{
  background: $violet;
  position: fixed;
  top: 0;
  right: 0;
  max-width: 320px;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: transform 500ms ease-in-out 0ms;
  transform: translateX(100%);
}

.navigation__list{
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: max-content;
  max-height: 100%;
  margin: 0;
  padding: 115px 0 40px;
  list-style: none;
  z-index: 2;
  overflow: auto;
  box-sizing: border-box;
}

.navigation__li{
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 0 0 35px;
}

.navigation__link{
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $white;
}

.navigation__overlay{
  position: fixed;
  display: none;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: transparent;
  opacity: 0;
  z-index: 1;
}

.navigation_shown{

  .buttons__nav-call{

    .button__line {
      background-color: transparent;
    }

    .button__line::before {
      top: 0;
      bottom: 0;
      transform: rotate(45deg);
    }

    .button__line::after {
      top: 0;
      bottom: 0;
      transform: rotate(-45deg);
    }
  }

  .navigation{
    transform: translateX(0);
  }

  .navigation__overlay {
    display: block;
  }
}

.banner-block{
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  height: 340px;
  overflow: hidden;
}

.banner-block__img-wrap{
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  z-index: -1;

  &:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    //background: rgba($greyDark,0.8);
    background: linear-gradient(0deg, rgba($black, 0.6), rgba($black, 0.6));
    background-blend-mode: multiply, normal;
    z-index: 1;
  }
}

.banner-block__img {
  position: relative;
  height: 100%;
  width: auto;
  object-fit: cover;
  left: 0;
  transform: translateX(-28%);
}

.banner-block__inner{
  position: relative;
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding: 0 15px;
}

.banner-block__title{
  padding: 0 0 20px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $white;
}

.banner-block__text{
  padding: 0 0 12px;
  ont-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $white;
}

.banner-block__text + .banner-block__text{
  padding: 0 0 16px;
}

.banner-block__buttons{
  display: flex;
  justify-content: center;
  padding: 0 0 33px;
}

.banner-block__button{
  padding: 8px 35px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.01em;
  background: transparent;
  border: 1px solid $violet;
  box-sizing: border-box;
  border-radius: 35px;
  color: $white;
  outline: none;
}

.banner-block__button_absolute{
  position: absolute;
  right: 12px;
  bottom: 10px;
  width: 30px;
  height: 30px;
}

.banner-block__button_orange{
  padding: 8px;
  line-height: 14px;
  background: $orange;
  border: 1px solid $orange;
}

.about-block{
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding: 15px;
}

.about-block__text-banner{
  padding: 0 0 15px;
}

.text-banner__text{
  padding: 0 0 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.06em;
  color: rgba($violet,0.2);
}

.text-banner__text_center{
  text-align: center;
}

.text-banner__text_end{
  text-align: right;
}

.about-block__title{
  padding: 0 0 12px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: $violet;
}

.about-block__desc{
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 170%;
  letter-spacing: 0.06em;
}

.about-block__desc_violet{
  color: $violet;
}

.comment-block{
  max-width: 320px;
  width: 100%;
  padding: 15px;
  margin: 0 auto;
  background: $violet;
}

.person-block + .person-block{
  margin: 40px 0 0;
}

.person-block__img-wrap{
  padding: 0 0 15px;
}

.person-block__img-circle{
  width: 80px;
  height: 80px;
  border: 5px solid $grey;
  border-radius: 50%;
  overflow: hidden;
}

.person-block__img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.person-block__comment{
  padding: 0 0 15px;
  font-family: Roboto;
  font-style: italic;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  letter-spacing: 0.06em;
  color: $white;
}

.person-block__profile{
  position: relative;
  padding: 0 0 18px;
  margin: 0 0 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 150%;
  letter-spacing: 0.06em;
  color: $white;

  &:before{
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 80px;
    height: 4px;
    background: $blue;
  }
}

.row__title,
.row__title-partners{
  padding: 50px 15px 20px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  content: $black;
}

.slider-block{
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding: 15px;

  .swiper-button-next,
  .swiper-button-prev{
    width: 27px;
    height: 27px;
    border-radius: 50%;
    background: $grey;
    outline: none;

    &:after{
      font-size: 10px;
      color: $black;
    }
  }
}

.slider-block__title{
  padding: 0 0 20px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: -0.01em;
  color: $violet;
}

.multiple-slides{
  padding: 0 70px;
}

.slider-block__sponsors-item{
  display: flex;
  justify-content: center;
}

.row_dark{
  background: $black;
}

.social-block{
  position: relative;
  display: flex;
  justify-content: center;
  padding: 40px 15px 18px;

  &:before{
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 94px;
    height: 4px;
    background: $violet;
    transform: translateX(-50%);
  }
}

.social-block__item{
  padding: 0 9px;
}

.social-block__link{
  display: block;
  width: 30px;
  height: 30px;
}

.social-block__img{
  width: 100%;
  height: 100%;
}

.info-block{
  padding: 35px 15px 20px;
}

.info-block__text{
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: -0.01em;
  color: $white;
}

.contact-block{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 15px;
}

.contact-block__address,
.contact-block__phone-title,
.contact-block__phone-number a,
.contact-block__mail-title{
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: -0.01em;
  color: $white;
}

.contact-block__mail-address a{
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: -0.01em;
  color: $violet;
}

.privacy-block{
  display: flex;
  justify-content: center;
  padding: 20px 10px;
}

.privacy-block__item{
  display: flex;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: -0.01em;
  color: $white;

  span{
    display: block;
    width: 15px;
  }

}

.privacy-block__link{
  display: block;
  color: $orange;
}

.copyright-block{
  padding: 15px 15px 35px;
}

.copyright-block__text{
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: -0.01em;
  color: $white;
}