@media screen and (min-width: 560px) {
  .banner-block__img {
    transform: translateX(-10%);
  }
}

@media screen and (min-width: 640px) {

  .logo__text{
    font-size: 16px;
    line-height: 19px;
  }

  .header__inner,
  .about-block,
  .slider-block{
    max-width: 590px;
    padding: 15px 0;
  }

  .banner-block__inner{
    max-width: 590px;
    padding: 0;
  }

  .banner-block__img {
    transform: initial;
  }

  .banner-block__title{
    font-size: 20px;
    line-height: 23px;
  }

  .banner-block__button_absolute{
    right: 0;
    width: 46px;
    height: 46px;
  }

  .banner-block__button_orange{
    padding: 8px;
    font-size: 20px;
    line-height: 100%;
  }

  .comment-block{
      max-width: 590px;
  }

  .buttons__nav-call{
    width: 30px;
    height: 22px;

    .button__line:before{
      top: -19px;
    }

    .button__line:after{
      bottom: -19px;
    }
  }

  .multiple-slides{
    padding: 0 90px;
  }

  .social-block__link{
    width: 35px;
    height: 35px;
  }
}

@media screen and (min-width: 768px) {

  .logo__text{
    font-size: 18px;
    line-height: 21px;
  }

  .header__inner,
  .banner-block__inner,
  .about-block,
  .comment-block,
  .slider-block{
    max-width: 650px;
  }

  .header__inner{
    padding: 20px 0;
  }

  .banner-block{
    height: 409px;
  }

  .banner-block__img{
    width: 100%;
  }

  .banner-block__title{
    font-size: 24px;
    line-height: 28px;
  }

  .banner-block__text{
    font-size: 16px;
    line-height: 19px;
  }

  .banner-block__buttons{
    padding: 0 0 41px;
  }

  .banner-block__button_absolute{
    bottom: 16px;
  }

  .comment-block{
    display: flex;
    justify-content: space-between;
  }

  .person-block{
    max-width: 280px;
    width: 100%;
  }

  .person-block + .person-block{
    margin: 0;
  }

  .person-block__img-wrap {
    padding: 0 0 25px;
  }

  .person-block__img-circle{
    width: 108px;
    height: 108px;
  }

  .slider-block{
    padding: 20px 0;
  }

  .slider-block + .slider-block{
    padding: 20px 0 70px;
  }

  .text-banner__text,
  .about-block__title,
  .row__title,
  .row__title-partners{
    font-size: 24px;
    line-height: 28px;
  }

  .slider-block__title{
    font-size: 22px;
    line-height: 26px;
  }

  .multiple-slides{
    padding: 0 30px;
    margin: 0;
  }

  .swiper-button-prev, .swiper-container-rtl .swiper-button-next{
    left: 0;
  }

  .swiper-button-next, .swiper-container-rtl .swiper-button-prev{
    right: 0;
  }

  .info-block__text{
    font-size: 20px;
    line-height: 23px;
  }

  .social-block{
    padding: 50px 15px 18px;
  }

  .privacy-block{
    padding: 30px 15px;
  }

  .contact-block__address,
  .contact-block__mail-title,
  .contact-block__mail-address a,
  .contact-block__phone-title,
  .contact-block__phone-number a,
  .privacy-block__item,
  .copyright-block__text{
    font-size: 16px;
    line-height: 19px;
  }

}

@media screen and (min-width: 1024px) {

  .logo__text{
    font-size: 22px;
    line-height: 26px;
  }

  .navigation__overlay,
  .buttons__nav-call{
    display: none !important;
  }

  .navigation{
    position: relative;
    max-width: initial;
    width: max-content;
    transform: initial;
    background: transparent;
  }

  .navigation__list{
    padding: 0;
  }

  .navigation__li{
    width: max-content;
    margin: 0 25px 0 0;

    &:nth-last-child(1){
      margin: 0;
    }
  }

  .header__inner,
  .banner-block__inner,
  .about-block,
  .comment-block,
  .slider-block{
    max-width: 867px;
  }

  .header__inner{
    padding: 30px 0;
  }

  .banner-block{
    height: 545px;
  }

  .banner-block__title{
    padding: 0 0 28px;
    font-size: 36px;
    line-height: 42px;
  }

  .banner-block__text{
    font-size: 18px;
    line-height: 21px;
  }

  .banner-block__buttons{
    padding: 0 0 29px;
  }

  .banner-block__button{
    padding: 17px 45px;
    font-size: 16px;
    line-height: 19px;
  }

  .banner-block__button_absolute{
    bottom: 20px;
    width: 54px;
    height: 54px;
  }

  .banner-block__button_orange{
    padding: 8px;
    font-size: 28px;
  }

  .comment-block{
    padding: 35px;
  }

  .text-banner__text{
    font-size: 32px;
    line-height: 37px;
  }

  .about-block__title,
  .row__title,
  .row__title-partners{
    font-size: 36px;
    line-height: 42px;
  }

  .about-block__desc{
    font-size: 16px;
    line-height: 170%;
  }

  .person-block{
    max-width: 363px;
  }

  .slider-block__title{
    font-size: 30px;
    line-height: 35px;
  }

  .multiple-slides__img{
    width: 197px;
    height: 197px;
  }

  .social-block__item{
    padding: 0 6px;
  }

  .social-block__link {
    width: 45px;
    height: 45px;
  }

  .info-block__text{
    font-size: 28px;
    line-height: 33px;
  }

  .privacy-block {
    padding: 35px 15px 105px;
  }

  .contact-block__address,
  .contact-block__mail-title,
  .contact-block__mail-address a,
  .contact-block__phone-title,
  .contact-block__phone-number a,
  .copyright-block__text {
    font-size: 20px;
    line-height: 23px;
  }

  .privacy-block__item{
    font-size: 22px;
    line-height: 26px;
  }
}

@media screen and (min-width: 1200px) {
  .header__inner,
  .banner-block__inner,
  .about-block,
  .comment-block,
  .slider-block{
    max-width: 1043px;
  }

  .banner-block{
    height: 639px;
  }

  .banner-block__title{
    padding: 0 0 36px;
    font-size: 48px;
    line-height: 56px;
  }

  .banner-block__text{
    font-size: 22px;
    line-height: 26px;
  }

  .banner-block__buttons{
    padding: 0 0 65px;
  }

  .banner-block__button{
    padding: 21px 73px;
    font-size: 18px;
    line-height: 21px;
  }

  .banner-block__button_absolute{
    width: 70px;
    height: 70px;
  }

  .banner-block__button_orange{
    padding: 8px;
    font-size: 35px;
  }

  .multiple-slides{
    padding: 0 50px;
  }

  .text-banner__text{
    font-size: 38px;
    line-height: 45px;
  }

  .about-block__title,
  .row__title,
  .row__title-partners{
    font-size: 42px;
    line-height: 49px;
  }

  .about-block__desc{
    font-size: 18px;
  }

  .person-block__comment,
  .person-block__profile{
    font-size: 16px;
  }

  .person-block{
    max-width: 452px;
  }

  .slider-block__title{
    font-size: 32px;
    line-height: 37px;
  }

  .social-block__link {
    width: 53px;
    height: 53px;
  }

  .info-block__text{
    font-size: 31px;
    line-height: 36px;
  }

  .contact-block__address,
  .contact-block__mail-title,
  .contact-block__mail-address a,
  .contact-block__phone-title,
  .contact-block__phone-number a,
  .copyright-block__text {
    font-size: 22px;
    line-height: 26px;
  }

  .privacy-block__item{
    font-size: 24px;
    line-height: 28px;
  }

}

@media screen and (min-width: 1360px) {
  .header__inner,
  .banner-block__inner,
  .about-block,
  .comment-block,
  .slider-block{
    max-width: 1200px;
  }

  .row_inner__flex-center{
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .col_6{
    width: 100%;
    max-width: 600px;
  }

  .about-block{
    padding: 0 44px 0 0;
  }

  .comment-block{
    flex-wrap: wrap;
    padding: 55px 90px 75px 55px;
  }

  .person-block + .person-block{
    margin: 40px 0 0;
  }

}

@media screen and (min-width: 1920px) {
  .header__inner,
  .about-block,
  .comment-block{
    max-width: 1360px;
  }

  .banner-block__inner{
    max-width: 1740px;
  }

  .banner-block{
    height: 803px;
  }

  .banner-block__title{
    padding: 0 0 37px;
    font-size: 60px;
    line-height: 70px;
  }

  .banner-block__text{
    font-size: 24px;
    line-height: 28px;
  }

  .banner-block__buttons{
    padding: 0 0 105px;
  }

  .banner-block__button{
    padding: 17px 45px;
    font-size: 18px;
    line-height: 21px;
  }

  .banner-block__button_absolute{
    bottom: 20px;
    width: 54px;
    height: 54px;
  }

  .banner-block__button_orange{
    padding: 8px;
    font-size: 35px;
  }

  .col_6{
    max-width: 680px;
  }

  .comment-block{
    padding: 100px 30px 100px 40px;
  }

  .person-block{
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: auto auto;
    grid-template-areas:
            'img comment'
            'img profile';

    max-width: initial;
  }

  .person-block__img-wrap{
    grid-area: img;
    padding: 0 45px 0 0;
  }

  .person-block__comment{
    grid-area: comment;
  }

  .person-block__profile{
    grid-area: profile;
  }

  .slider-block{
    max-width: 1740px;
  }

  .multiple-slides{
    padding: 0 190px;
  }

  .text-banner__text{
    font-size: 44px;
    line-height: 52px;
  }

  .about-block__title,
  .row__title,
  .row__title-partners{
    font-size: 48px;
    line-height: 56px;
  }

  .slider-block__title{
    font-size: 36px;
    line-height: 42px;
  }

  .info-block__text{
    font-size: 35px;
    line-height: 41px;
  }

  .contact-block__address,
  .contact-block__mail-title,
  .contact-block__mail-address a,
  .contact-block__phone-title,
  .contact-block__phone-number a,
  .copyright-block__text,
  .privacy-block__item{
    font-size: 24px;
    line-height: 28px;
  }
}
